// Fonts
@import '../newui/_variables';
@import '../newui/_mixins';
@import '../newui/_vuewind';

#viewport {
  display: flex;
  height: 100%;
  width: 100%;
  @include transition(200ms);
  flex: 1;
}

.form-fixed-width {
  width: 700px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 25px;
}

.form-group-label {
  @include font-size(18);
  font-weight: 800;
  letter-spacing: 0.3px;
  margin-bottom: 25px;
  display: block;
}

.menu-list-wrapper {

  &.vertical {
    margin-bottom: 20px;

    .menu-list-item {
      display: block;
      padding: 12px 15px 12px 25px;
    }
  }

  &.horizontal {
    display: flex;
    border-bottom: 2px solid $light_mode_border;
    margin-bottom: 30px;
    overflow-x: auto;
    z-index: 9;
    position: sticky;
    background: white;
    top: 40px;

    &::-webkit-scrollbar {
      display: none;
    }

    .menu-list-item {
      display: inline-block;
      padding: 15px 10px;
      margin: 15px 10px 0;
      border-bottom: 2px solid transparent;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .menu-list-item {
    text-decoration: none;
    cursor: pointer;
    position: relative;
    white-space: nowrap;

    &.link {
      display: flex;
      align-items: center;

      &.is-active,
      &.router-link-exact-active,
      &:hover {

        svg {
          path, line, polyline, rect, circle, ellipse {
            color: inherit;
          }
        }

        .label {
          color: inherit;
        }
      }

      .icon {
        margin-right: 12px;
        line-height: 0;

        path, line, polyline, rect, circle, ellipse {
          color: inherit;
        }
      }

      .text-label {
        @include font-size(16);
      }
    }

    &:hover,
    &.is-active {

      .delete-icon {
        display: block;
      }
    }

    .folder-icon {
      line-height: 0;
      width: 15px;
      margin-right: 9px;
      vertical-align: middle;
      margin-top: -1px;

      path {
        color: inherit;
      }
    }

    .delete-icon {
      display: none;
      position: absolute;
      right: 15px;
      top: 50%;
      @include transform(translateY(-50%));
    }

    .label {
      @include font-size(13);
      font-weight: 700;
      vertical-align: middle;
      white-space: nowrap;
      max-width: 210px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: inline-block;
      color: $text;
      line-height: 1;
      padding-top: 2px;
    }
  }

  &.favourites {

    &.is-dragenter .menu-list {
      border-radius: 8px;
    }

    .menu-list {
      border: 2px dashed transparent;

      .menu-list-item {
        padding: 8px 23px;

        .label {
          max-width: 140px;
        }

        .icon {
          margin-right: 5px;
          width: 20px;

          path, line, polyline, rect, circle, ellipse {
            @include transition(150ms);
          }
        }

        &:hover,
        &.is-selected,
        &.is-current {

          .folder-icon {
            path, line, polyline, rect, circle, ellipse {
              color: inherit;
            }
          }

          .label {
            color: inherit;
          }
        }
      }
    }
  }
}

.empty-note {
  @include font-size(12);
  color: $text-muted;
  display: block;
}

.table {

  .action-icons {
    white-space: nowrap;

    a, .icon, .icon-wrapper {
      display: inline-block;
      margin-left: 10px;

      &:first-child {
        margin-left: 0;
      }
    }

    .icon {
      cursor: pointer;

      circle, path, line, polyline {
        stroke: $text;
      }

      &.icon-trash {
        circle, path, line, polyline {
          stroke: $red;
        }
      }
    }
  }

  .cell-item {
    white-space: nowrap;
    //color: $text;
    font-weight: 700;
  }
}

// Helpers
.mt-70 {
  margin-top: 70px;
}

.capitalize {
  text-transform: capitalize;
}

@media only screen and (max-width: 1024px) {

  .menu-list-wrapper {

    .menu-list-item {
      padding: 12px 15px 12px 20px;
    }

    &.favourites {

      .menu-list .menu-list-item {
        padding: 8px 18px;
      }
    }
  }
}

@media only screen and (max-width: 960px) {

  .menu-list-wrapper {
    &.horizontal {
      top: 30px;
    }
  }

  .form-fixed-width {
    width: 100%;
  }
}

.dark {

  .empty-note {
    color: $dark_mode_text_secondary;
  }

  .menu-list-wrapper {

    &.horizontal {
      border-bottom: 2px solid $dark_mode_border_color;
      background: $dark_mode_background;
    }

    .menu-list-item {

      &.link {

        .icon {

          path, line, polyline, rect, circle, ellipse {
            color: inherit;
          }
        }
      }

      .label {
        color: $dark_mode_text_primary;
      }

      .icon {

        path, line, polyline, rect, circle, ellipse {
          color: inherit;
        }
      }

      &:hover {

        .icon {

          path, line, polyline, rect, circle, ellipse {
            color: inherit;
          }
        }
      }
    }
  }

  .table {

    .action-icons {

      .icon {

        circle, path, line, polyline {
          stroke: $dark_mode_text_primary;
        }
      }
    }
  }

}

.info-box-wrapper {
  margin: 0px 20px;
}

.windows {
  
  #content-sidebar {
    scrollbar-width: none;
    &::-webkit-scrollbar {
      width: 0px;
    }
  }

  ::-webkit-scrollbar {
    width: 18px;
    height: 18px;
    cursor: pointer;
  }

  ::-webkit-scrollbar-thumb {
      border: 6px solid transparent;
      background: #7f7f7f;
      border-radius: 25px;
      background-clip: padding-box ;
  }
}

.dark {
  .windows {
    ::-webkit-scrollbar-thumb {
      border: 6px solid transparent;
      background: $dark_mode_foreground !important;
      background-clip: padding-box !important ;
    }
  }
}

/* Tailwind Helpers */
.router-link-active .icon-active {
  color: #9d66fe;
}

.router-link-active .text-active {
  color: #9d66fe;
}

/* Group options */
.group:hover:not(.hover-disabled) .group-hover-text-theme {
  color: #9d66fe !important;
}

/* Single option */
.svg-color-theme {
  fill: #9d66fe;
  stroke: #9d66fe;
}

.svg-color-theme-darken {
  fill: #9d66fe;
  stroke: #9d66fe;
  filter: brightness(0.80);
}

.svg-stroke-theme {
  stroke: #9d66fe
}

.hover-svg-stroke-theme:hover {
  stroke: #9d66fe
}

.hover-svg-stroke-theme:hover rect {
  stroke: #9d66fe
}

.hover-svg-stroke-theme:hover line {
  stroke: #9d66fe
}

.bg-theme {
  background: #9d66fe
}

.bg-theme-50 {
  background: #9d66fe05
}

.bg-theme-100 {
  background: #9d66fe10
}

.bg-theme-200 {
  background: #9d66fe20
}

.bg-theme-800 {
  background: #9d66fe80
}

.hover-bg-theme:hover {
  background: #9d66fe
}

.hover-bg-theme-100:hover {
  background: #9d66fe10 !important;
}

.text-theme {
  color: #9d66fe
}

.text-theme-darken {
  color: #9d66fe;
  filter: brightness(0.75);
}

.hover-text-theme:hover {
  color: #9d66fe
}

.shadow-theme {
  box-shadow: 0 10px 15px -3px #9d66fe40
}

.border-theme {
  border-color: #9d66fe
}

.border-top-theme {
  border-top-color: #9d66fe !important;
}

.border-left-theme {
  border-left-color: #9d66fe !important;
}

.hover-border-theme:hover {
  border-color: #9d66fe !important;
}

.focus-within-border-theme:focus-within {
  border-color: #9d66fe !important;
}

.focus-border-theme:focus {
  border-color: #9d66fe !important
}

.focus-border-theme:focus[type='email'] {
  border-color: #9d66fe
}

.focus-border-theme:focus[type='text'] {
  border-color: #9d66fe
}

.focus-border-theme:focus[type='password'] {
  border-color: #9d66fe
}

.focus-border-theme:focus[type='number'] {
  border-color: #9d66fe
}

/* Global Active Class */
.active.active-bg-theme-100 {
  background: #9d66fe10 !important;
}

.active.active-border-theme {
  border-color: #9d66fe !important;
}

.active .active-text-theme {
  color: #9d66fe
}

/* Emoji Picker */
.active-menu.focus-border-theme {
  border-color: #9d66fe !important;
}

/* Sidebar Navigation */
.router-link-active.home .button-icon {
  background: #9d66fe10
}

/* Content Panel */
.router-link-active .text-theme {
  color: #9d66fe !important
}

.router-link-active .text-theme svg {
  color: #9d66fe !important
}

.router-link-active.border-bottom-theme {
  border-bottom-color: #9d66fe !important;
}

.is-active .text-theme {
  color: #9d66fe !important
}

.is-active .text-theme svg {
  color: #9d66fe !important
}

.menu-list-item.link:hover {
  color: #9d66fe
}

.menu-list-item.link:hover .text-theme svg {
  color: #9d66fe
}

/* Select Input */
.input-area.is-active {
  border-color: #9d66fe !important
}

/* ButtonBase */
.button-base.theme-solid {
  background: #9d66fe
}

.button-base.theme-solid polyline,
.button-base.theme-solid path {
  color: white;
}


.button-base.theme {
  background: #9d66fe10
}

.button-base.theme {
  color: #9d66fe
}

.button-base.theme .content {
  color: #9d66fe
}

.button-base.theme polyline,
.button-base.theme path {
  color: #9d66fe
}

.switch.active {
  background: #9d66fe !important;
}


/* Dragged borders */
.border-theme {
  border-color: #9d66fe !important;
}

.folder-item.is-dragenter {
  border-color: #9d66fe !important;
}

.favourites.is-dragenter .menu-list {
  border-color: #9d66fe !important;
}

/* Stripe */
.StripeElement--focus {
  border-color: #9d66fe !important;
}

/* Dark mode */
.dark .dark-text-theme {
  color: #9d66fe !important;
}

.dark .text-theme {
  color: #9d66fe
}
